header {
  /* border: 10px solid red; */
  width: 100%;
  height: 100px;
  margin: 0% auto;
  position: fixed;
  background-color: rgba(0, 0, 0, 1);
  height: 100px;
  z-index: 100;
}

header nav ul.m-title {
  display: none;
}


header div.outer {
  /* border: 1px solid orange; */
  width: 85.42%;
  max-width: 1640px;
  height: 100%;
  margin: 0% auto;
  position: relative;
  overflow: hidden;
}

header div.outer h1 {
  /* border: 1px solid red; */
  float: left;
  padding: 19px 0;
}

header div.outer h1 img {
  width: 100px;
  height: 60px;
}

header div.outer button img {
  /* border: 1px solid red; */
  width: 46px;
  height: 24px;
  position: absolute;
  left: 130px;
  top: 38px;
}

header div.outer div#Right {
  /* border: 1px solid saddlebrown; */
  float: right;
}

header div.outer div#Right ul {
  /* border: 1px solid red; */
  padding: 29px 0;
}

header div.outer div#Right ul li {
  /* border: 3px solid blue; */
  display: inline-block;
  vertical-align: middle;
}


header div.outer div#Right ul li a {
  /* border: 3px solid green; */
}

header div.outer div#Right ul li a img {
  width: 30px;
  height: 30px;
  margin-right: 30px;
}

header div.outer div#Right ul li a img.user {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 0;
}

header div.outer div#Right ul li input[type=image] {
  /* border: 3px solid red; */
  width: 30px;
  height: 30px;
  margin: 3px 30px 0 0;
}

header div.outer div.inner {
  /* border: 1px solid orange; */
  width: 84.15%;
  height: 100%;
  margin: 0% auto;
  position: relative;
}

header div.outer div.inner nav {
  /* border:3px solid green; */
}

header div.outer div.inner nav ul.title {
  float: left;
  padding: 18px 0;
  margin-left: 160px;
}

header div.outer div.inner nav ul.title li {
  padding: 0 40px;
  display: inline-block;
}

header div.outer div.inner nav ul.title li a {
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-size: 20px;
  color: #fff;
  letter-spacing: -0.02em;
}

header div.outer div.inner nav ul.title li a:last-child {
  padding-right: 0;
}

.nav {
  display: none;
}

.nav-toggle {
  display: none;
}


