#visual {
  width: 100%;
  height: auto;
  margin: 0 auto;
  background: #fff;
  background-size: cover;
  overflow: hidden;
  position: relative;
  display: flex;
  align-items: center;
  padding-top: 100px;
  z-index: 10;
}

#visual video {
  width: 100%;
  height: 100%;
  /* border:10px solid red; */
  object-fit: cover;
  object-position: 50% 0;
}

#visual div.t-visual {
  display: none;
}

#visual div.m-visual {
  display: none;
}

#c01 {
  width: 100%;
  height: auto;
  background: #f8f9fa;
  padding: 150px 0;
  position: relative;
  /* border: 10px solid magenta; */
}

#c01 div.outer {
  width: 85.42%;
  max-width: 1640px;
  height: 100%;
  margin: 0 auto;
  position: relative;
  /* border: 10px solid red; */
}

#c01 div.outer div.inner {
  /* border: 1px solid red; */
  width: 84.15%;
  height: 100%;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
}

#c01 div.outer div.inner h2 {
  font-family: 'Noto Sans KR', serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-style: normal;
  font-size: 38px;
  letter-spacing: -0.02em;
}

#c01 div.outer div.inner div.btn {
  width: 140px;
  height: 40px;
  background: #eee;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  position: absolute;
  right: 0;
  top: 0;
}

#c01 div.outer div.inner div.btn button {
  border: none;
  background: #eee;
  cursor: pointer;
  font-family: 'Noto Sans KR', serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  color: #111;
  letter-spacing: -0.02em;
  display: flex;
  align-items: center;
  justify-content: center;
}

#c01 div.outer div.inner div.btn button:nth-child(1) {
  width: 68px;
  height: 36px;
  border: none;
  background-color: #007bff;
  color: #fff;
  border-radius: 20px;
  cursor: pointer;
  white-space: nowrap;
}

#c01 div.outer div.inner div.btn button:nth-child(2) {
  width: 68px;
  height: 36px;
  border-radius: 20px;
  border: none;
  cursor: pointer;
}

#c01 div.outer div.inner div.btn button:hover {
  background-color: #0056b3;
  border-radius: 20px;
}

#c01 .tabs {
  display: flex;
  margin: 46px 0 80px;
}

#c01 .tab {
  padding: 10px 20px;
  cursor: pointer;
  border-bottom: 2px solid transparent;
  transition: all 0.3s ease;
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 300;
  font-style: normal;
  font-size: 18px;
  letter-spacing: -0.02em;
}

#c01 .tab.active {
  border-bottom: 2px solid #74c3ff;
  display: flex;
}

#c01 .swiper-section {
  /* border: 5px solid red; */
  display: none;
}

